import Debug from "../utility/Debug";
import XM from "./XM";
export default class XMConnect {
    /**
     * Make a cross-domain xmlHttpRequest.
     * For userscripts, the domain name MUST be defined in @resource tag.
     * For extensions, the domain name MUST be listed in the permissions.
     * @param details Request details
     */
    static xmlHttpRequest(details) {
        Debug.connectLog(details.url);
        GM_xmlhttpRequest(XMConnect.validateXHRDetails(details));
    }
    /**
     * Cross-domain xmlHttpRequest, wrapped in a Promise.
     * For userscripts, the domain name MUST be defined in @resource tag.
     * For extensions, the domain name MUST be listed in the permissions.
     * @param details Request details
     */
    static xmlHttpPromise(details) {
        const validDetails = XMConnect.validateXHRDetails(details);
        return new Promise((resolve, reject) => {
            const callbacks = {
                onabort: validDetails.onabort,
                onerror: validDetails.onerror,
                onload: validDetails.onload,
                onloadstart: validDetails.onloadstart,
                onprogress: validDetails.onprogress,
                onreadystatechange: validDetails.onreadystatechange,
                ontimeout: validDetails.ontimeout,
            };
            details.onabort = (event) => { callbacks.onabort(event); reject(event); };
            details.onerror = (event) => { callbacks.onerror(event); reject(event); };
            details.onload = (event) => { callbacks.onload(event); resolve(event); };
            details.onloadstart = (event) => { callbacks.onloadstart(event); };
            details.onprogress = (event) => { callbacks.onprogress(event); };
            details.onreadystatechange = (event) => { callbacks.onreadystatechange(event); };
            details.ontimeout = (event) => { callbacks.ontimeout(event); reject(event); };
            XMConnect.xmlHttpRequest(validDetails);
        });
    }
    /**
     * Validates the xmlHttpRequest details, returning a valid set
     * @param details Request details
     */
    static validateXHRDetails(details) {
        if (details.headers === undefined)
            details.headers = {};
        if (details.headers["User-Agent"] === undefined) {
            details.headers["User-Agent"] = window["re621"]["useragent"];
            details.headers["X-User-Agent"] = window["re621"]["useragent"];
        }
        if (details.onabort === undefined)
            details.onabort = () => { return; };
        if (details.onerror === undefined)
            details.onerror = () => { return; };
        if (details.onload === undefined)
            details.onload = () => { return; };
        if (details.onloadstart === undefined)
            details.onloadstart = () => { return; };
        if (details.onprogress === undefined)
            details.onprogress = () => { return; };
        if (details.onreadystatechange === undefined)
            details.onreadystatechange = () => { return; };
        if (details.ontimeout === undefined)
            details.ontimeout = () => { return; };
        return details;
    }
    static download(a, b) {
        if (typeof a === "string") {
            a = {
                url: a,
                name: b,
            };
        }
        if (a.headers === undefined)
            a.headers = {
                "User-Agent": window["re621"]["useragent"],
                "X-User-Agent": window["re621"]["useragent"],
            };
        if (a.onerror === undefined)
            a.onerror = () => { return; };
        if (a.onload === undefined)
            a.onload = () => { return; };
        if (a.onprogress === undefined)
            a.onprogress = () => { return; };
        if (a.ontimeout === undefined)
            a.ontimeout = () => { return; };
        let timer;
        XMConnect.xmlHttpRequest({
            url: a.url,
            method: "GET",
            headers: a.headers,
            responseType: "blob",
            onerror: (event) => { a.onerror(event); },
            ontimeout: (event) => { a.ontimeout(event); },
            onprogress: (event) => {
                if (timer)
                    clearTimeout(timer);
                timer = window.setTimeout(() => { a.onprogress(event); }, 500);
            },
            onload: (event) => {
                a.onload(event);
                const btn = $("<a>")
                    .attr({
                    href: URL.createObjectURL(event.response),
                    download: a.name,
                })
                    .html("download")
                    .on("click", () => { btn.remove(); });
                btn[0].click();
            }
        });
    }
    static browserDownload(a, b, c) {
        // Fallback to avoid a crash in Vivaldi
        if (Debug.getState("vivaldi"))
            XM.Connect.download(a, b);
        const downloadDetails = typeof a === "string"
            ? { url: a, name: b, saveAs: c }
            : a;
        // Workaround to SWF files not being whitelisted by default in Tampermonkey
        downloadDetails.onerror = (event) => {
            if (event.error == "not_whitelisted")
                XM.Connect.download(a, b);
            else if (a.onerror)
                a.onerror(event);
            else
                throw "Error: unable to download file" + (event.error ? (` [${event.error}]`) : "");
        };
        // All script managers should have a GM_download function
        GM_download(a);
    }
}
